/* ================= GLOBAL RESET ================= */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
  background: #f8fafc;
  color: #1e293b;
}

/* ================= NAVBAR ================= */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 65px;
  padding: 0 2rem;

  background: linear-gradient(135deg, #0f3d4a, #1b5f63, #9c4f2f);
  box-shadow: 0 8px 25px rgba(0,0,0,0.15);

  display: flex;
  justify-content: space-between;
  align-items: center;

  z-index: 100;
}

.nav-brand {
  color: white;
  font-size: 1.4rem;
  font-weight: 700;
}

.nav-links {
  display: flex;
  gap: 20px;
  align-items: center;
}

.nav-link-btn {
  color: white;
  background: transparent;
  border: none;
  font-size: 1rem;
  cursor: pointer;
}

.btn-contact {
  background: rgba(255,255,255,0.2);
  color: white;
  padding: 8px 18px;
  border-radius: 50px;
  border: 1px solid rgba(255,255,255,0.4);
  cursor: pointer;
}

/* ================= HERO SECTION ================= */
.hero-section {
  padding: 150px 20px 80px;
  text-align: center;

  background: linear-gradient(#f0f9ff, #e0f2fe);
}

.hero-title {
  font-size: 2.8rem;
  font-weight: 700;
  margin-bottom: 20px;
}

.hero-subtitle {
  max-width: 700px;
  margin: auto;
  font-size: 1.1rem;
  color: #475569;
  line-height: 1.6;
}

.hero-buttons {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  gap: 20px;
}

.hero-buttons button {
  padding: 12px 25px;
  border-radius: 25px;
  border: 2px solid #0f3d4a;
  background: white;
  cursor: pointer;
}

/* ================= PRODUCTS SECTION ================= */
.products-section {
  padding: 80px 0;
  text-align: center;
}

.section-title {
  font-size: 2rem;
  font-weight: 700;
}

/* ARROWS */
.slider-arrows {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 15px;
}

.slider-arrows button {
  font-size: 22px;
  padding: 10px 14px;
  border-radius: 8px;
  background: white;
  border: 1px solid #e2e8f0;
  box-shadow: 0 3px 10px rgba(0,0,0,0.1);
  cursor: pointer;
}

/* HORIZONTAL SLIDER */
.products-grid {
  margin-top: 40px;
  padding: 10px;

  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
  gap: 25px;
}

.products-grid::-webkit-scrollbar {
  height: 8px;
}

.products-grid::-webkit-scrollbar-thumb {
  background: #94a3b8;
  border-radius: 10px;
}

/* PRODUCT CARD */
.product-card {
  min-width: 320px;
  max-width: 320px;
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 16px;
  overflow: hidden;
  flex-shrink: 0;

  transition: 0.3s;
}

.product-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 30px rgba(0,0,0,0.1);
}

.product-image {
  width: 100%;
  height: 220px;
  object-fit: cover;
}

.product-content {
  padding: 20px;
  text-align: left;
}

.product-title {
  font-size: 1.2rem;
  font-weight: 700;
}

.product-desc {
  font-size: 0.9rem;
  color: #64748b;
  margin-top: 5px;
  margin-bottom: 15px;
}

.btn-chat {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  border: none;
  background: #e0f2fe;
  color: #0284c7;
  cursor: pointer;
}

/* ================= FLOATING CHAT BUTTON ================= */
.floating-bubble {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 60px;
  height: 60px;

  background: linear-gradient(135deg,#1b5f63,#9c4f2f);
  border: none;
  border-radius: 50%;
  color: white;

  font-size: 22px;
  cursor: pointer;

  box-shadow: 0 10px 25px rgba(0,0,0,0.3);
  z-index: 200;
}

.feature-box {
  padding: 20px 30px;
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  text-align: center;
  width: 200px;
  transition: 0.2s;
}

.feature-box:hover {
  transform: translateY(-6px);
  box-shadow: 0 12px 28px rgba(0,0,0,0.08);
}

.hero-wrapper h3 {
  font-size: 1.1rem;
  color: #0f172a;
  line-height: 1.4;
}
